// socket.js
import io from 'socket.io-client';

const socket = io('https://vala-score-be-prod.onrender.com', {
  reconnectionDelay: 1000, // wait 1 second before attempting to reconnect
  reconnectionDelayMax: 50000, // wait at most 50 seconds before attempting to reconnect
  randomizationFactor: 0 // disable randomization
});

export default socket;